/* src/Component/Services/Portfolio/Portfolio.css */
.portfolio-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background: #f7f9fc;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  .header {
    text-align: center;
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  .content {
    font-size: 1rem;
    line-height: 1.6;
    color: #555;
  }
  
  .content h2 {
    color: #007acc;
    font-size: 1.5rem;
    margin-top: 20px;
  }
  
  .content p {
    margin: 10px 0;
  }
  
  .content a {
    color: #007acc;
    text-decoration: none;
  }
  
  .content a:hover {
    text-decoration: underline;
  }
  
  .content ul {
    list-style-type: disc;
    padding-left: 20px;
  }
  
  .content code {
    background: #eef;
    padding: 2px 4px;
    border-radius: 4px;
  }
  