/* Paste the provided CSS styles here */
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-10px);
    }
    60% {
        transform: translateY(-5px);
    }
}

body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background-color: #f0f0f0;
}

nav {
    background-color: #333;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

nav ul li {
    margin: 0 10px;
}

nav ul li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 20px;
    text-decoration: none;
}

nav ul li a:hover {
    background-color: #575757;
}

.services-page {
    padding: 100px 20px 20px 20px;
    background-color: #f5f7f6; /* Adjusted background color */
    min-height: 80vh;
}

.services-header {
    text-align: center;
    margin-bottom: 20px;
}

.services-header h1 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #333;
}
@keyframes revealText {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}

.services-header p {
    font-size: 1.5em;
    color: #777;
    overflow: hidden; /* Ensure the text is hidden when not fully revealed */
    white-space: nowrap; /* Prevent text from wrapping to a new line */
    display: inline-block; /* Allow animation on width */
    width: 0;
    animation: revealText 10s steps(50, end) infinite; /* Animate the text reveal */
}


.services-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
}
@keyframes pulse {
    0% {
      background-color: linear-gradient(135deg, rgba(255, 200, 0, 0.8), rgba(220, 230, 180, 0.8));
    }
    50% {
      background-color: linear-gradient(135deg, rgba(255, 220, 0, 0.8), rgba(235, 240, 200, 0.8));
    }
    100% {
      background-color: linear-gradient(135deg, rgba(255, 200, 0, 0.8), rgba(220, 230, 180, 0.8));
    }
  }
  
  .service-card {
    background: linear-gradient(135deg, rgba(255, 200, 0, 0.8), rgba(220, 230, 180, 0.8)), linear-gradient(135deg, rgba(255, 180, 0), rgba(196, 201, 137));
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 300px;
    text-align: center;
    padding: 20px;
    position: relative;
    animation: pulse 6s infinite; /* Adjusted animation timing */
    transition: transform 0.5s, box-shadow 0.5s, filter 0.5s, color 0.5s; /* Add transition for smoother effects */
  }
  
.service-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #ffafbd, #ffc3a0); /* Colorful gradient effect */
    z-index: 1;
    opacity: 0;
    transition: opacity 0.3s;
}

.service-card:hover::before {
    opacity: 1;
}

.service-card:hover {
    transform: translateY(-10px) rotateY(180deg); /* Flip the card on hover */
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2); /* Increase shadow depth */
    filter: brightness(1.2); /* Increase brightness */
    color: white; /* Change text color to white on hover */
}

.service-card .front,
.service-card .back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backface-visibility: hidden; /* Ensure proper rendering of both faces */
}

.service-card .back {
    transform: rotateY(180deg); /* Initially hide the back face */
}

.service-card .service-details {
    position: relative;
    z-index: 2;
    transition: opacity 0.5s; /* Add transition for smoother effects */
}

.service-card:hover .service-details {
    opacity: 0; /* Hide the details on hover */
}

.service-icon {
    font-size: 3em;
    margin-bottom: 15px;
    color: #fff;
    position: relative;
    z-index: 2;
}

.service-details h2,
.service-details p {
    font-size: 1em;
    color: #666;
    margin: 0; /* Remove default margin */
}
